.home-container-1{
  min-height: 100vh;
  max-width: 1250px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin: 0% auto;
  flex: 1 0 auto;
}

.home-container-2 {
  max-width: 1100px;
  width: calc(100% - 164px);
  border-radius: 0;
  border: solid #d6d9dc;
  border-width: 0 0 0 1px;
  padding: 24px;
  box-sizing: border-box;
}